<template>
  <div id="register">
    <div class="main">
      <div class="register-main">
        <h3>注册</h3>
        <input type="tel" v-model="mobile"  maxlength="11" class="bb-ccc" placeholder="请输入手机号码">
        <div class="phone-code bb-ccc">
          <input type="number" v-model="code" maxlength="6" class="bb-ccc input-code" placeholder="请输入短信验证码">
          <div class="get-code" :class="{'c-999': count>0}" @click="getPhoneCode">{{count>0?'('+count+'s)':'获取验证码'}}</div>
        </div>
        <input type="password" v-model="password"  class="bb-ccc" placeholder="请设置密码（6-16位字母和数字组合）">
        <p class="remind" v-if="isSend">验证码已发送至手机：{{mobile}}</p>
        <div class="btn">下一步</div>
        <p class="agreement">已阅读并同意<span>《礼物飞注册协议》</span></p>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        mobile: '',
        code: '',
        password: '',
        isSend: false,
        count: 0
      }
    },
    watch: {},
    methods: {
      getPhoneCode (){
        if(this.count>0)
          return;

        this.count = 60;
        this.countDown()
      },
      countDown: function(){
        let $this = this;
        var cd = setInterval(function() {
          $this.count--;
          if($this.count <= 0){
            $this.count = 0;
            clearInterval(cd);
          }
        },1000)
      },
    }
  }
</script>

<style lang="scss">
  @import "../../sass/baseSass.scss";
  #register{
    height: 100%;
    background: #fff;
    width: 100%;

    .main{
      height: 100%;
      width: 100%;
      background: url("../../assets/home/logo.png") no-repeat center top;
      background-size: 100% auto;
    }
    .register-main{
      width: 8rem;
      height: 8.43rem;
      padding: 0.44rem;
      position: absolute;
      bottom: 2rem;
      left: 1rem;
      box-shadow: 0rem -0.07rem 0.13rem 0.01rem rgba(67, 67, 67, 0.06), 0rem 0.07rem 0.13rem 0rem rgba(0, 0, 0, 0.13);
      border-radius: 0.11rem;

      h3{
        text-align: center;
        font-size: 0.48rem;
        color: #de7fef;
        margin-bottom: 0.25rem;
      }
      .phone-code,
      input{
        width: 100%;
        height: 1.2rem;
        border: none;
        font-size: 0.37rem;
        color: #333;
        background: #fff;
        position: relative;
      }
      .bb-ccc{
        border-bottom: 1px #d8d8d8 solid;
      }
      input.input-code{
        width: 5.5rem;
      }
      .get-code{
        width: 2.5rem;
        height: 0.6rem;
        line-height: 0.6rem;
        text-align: center;
        font-size: 0.35rem;
        color: #de7fef;
        position: absolute;
        right: 0;
        top: 0.3rem;
        border-left: 1px #d8d8d8 solid;
      }
      .c-999{
        color: #999;
      }
    }
    .remind{
      font-size: 0.32rem;
      color: #666;
      margin-top: 0.3rem;

    }
    .btn{
      width: 6.93rem;
      height: 1.07rem;
      background-image: linear-gradient(135deg,
      #fe9bba 0%,
      #de7fef 100%);
      border-radius: 0.53rem;
      margin: 0 auto;
      line-height: 1.07rem;
      color: #fff;
      font-size: 0.43rem;
      text-align: center;
      margin-top: 0.53rem;
    }
    .agreement{
      font-size: 0.32rem;
      color: #666666;
      margin-top: 0.4rem;

      span{
        color: #de7fef;
      }
    }
  }

</style>



